<template>
  <div class="dots">
    <canvas ref="canvas"></canvas>
  </div>
</template>

<script>
import DotsRun from 'common/js/dotsRun.js'
export default {
  name: 'dots',
  methods: {
    initCanvasDotsRun() {
      this.dots = new DotsRun(this.$refs.canvas, {
        ArcColor: '#999',
        canvasWidth: 'full',
        canvasHeight: 'full',
        speedX: 2,
        speedY: 3,
        isRandomDots: true,
        lineLength: 200,
        mouseLineLength: 300,
        isClick: true
      });
    }
  },
  mounted() {
    this.$nextTick(() => this.initCanvasDotsRun())
    this.$isBlur((res) => {
      res ? this.dots.initCanvas() : this.dots.clear();
    })
  }
}
</script>

<style lang="less" scoped>
.dots {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  width: 100%;
  height: 100%;
  > canvas {
    width: 100%;
    height: 100%;
  }
}
</style>